.about {
  padding: 2.5rem 0;
  background-color: $white;
  color: $darkie;

  @media (min-width: $desktop) {
    padding: 10rem 0 0 0;
    display: grid;
    grid-template-columns: 44% 49% 7%;
    grid-template-rows: 55% 1fr 1fr;
  }

  &__aside {
    // loop to print aside varia
    @each $prop, $value in $aside {
      #{$prop}: $value;
    }
    padding-bottom: 4rem;
    justify-content: flex-end;

    @media (min-width: $desktop) {
      grid-column: 3;
      grid-row: 1/4;
    }

    .menu-line {
      width: 100px;
      height: 1px;
      background-color: $darkie;
      margin-left: 1rem;
    }
  }

  &__aside--desktop {
    @media (min-width: $desktop) {
      flex-direction: column;
      padding-bottom: 0;

      p {
        transform: rotate(-90deg);
        padding-left: 5rem;
      }

      .menu-line {
        width: 1px;
        height: 400px;
      }
    }
  }

  &__img {
    height: 24rem;
    padding-bottom: 3rem;
    display: block;
    margin: 0 auto;


    @media (min-width: $desktop) {
      margin: inherit;
      grid-column: 1;
    grid-row: 1;
    padding-left: 14rem;
    }
  }

  &__resume {
    display: flex;
    flex-direction: column;
    padding: 0 6rem;

    @media (min-width: $desktop) {
      grid-column: 2;
      grid-row: 1/3;

      p {
        font-size: 2.4rem;
      }
    }
  }

  &__info {
    font-family: $regular;
    font-size: 2rem;
    padding: 0 6rem;

    @media (min-width: $desktop) {
      font-size: 2.6rem;
      grid-column: 1;
     grid-row: 2;
      padding-left: 14rem;
    }

    &--title {
      padding-top: 1rem;

      @media (min-width: $desktop) {
        padding-bottom: 2rem;
      }
    }
  }

  &__companies {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;

    @media (min-width: $desktop) {
      flex-wrap: nowrap;
      justify-content: flex-start;
    }

    &--logo {
      padding-left: 1.5rem;
      padding-right: 1.5rem;
      padding-top: 0.1rem;

      @media (min-width: $desktop) {
        scale: 1.3;
        padding-left: 2rem;
        padding-right: 2rem;
        padding-top: 0.1rem;
      }
    }
    .kaiju {
      border-right: 1px $darkie solid;
      border-left: 1px $darkie solid;
      padding: 0 1.5rem 0 1.5rem;
    }

    .adalab {
      border-right: 1px $darkie solid;
    }

    .monografica {
      @media (min-width: $desktop) {
        padding: 0 1rem 0 3.3rem;
      }
    }

    .easdm {
      @media (min-width: $desktop) {
        padding: 0 5rem;
      }
    }

    .gestorex {
      @media (min-width: $desktop) {
        border-right: 1px $darkie solid;
      }
    }
  }

  &__button {
    width: 12rem;
    margin: 4rem auto;
    padding: 0.6rem 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $darkie;
    color: $lightie;
    font-size: 2rem;
    font-family: $regular;
    border-radius: 0.5rem;
    cursor: pointer;

    @media (min-width: $desktop) {
      grid-column: 2;
      grid-row: 2/3;
      height: 4rem;
      font-size: 3rem;
      width: 17rem;
      margin: inherit;
      margin-left: 6rem;
    }

    .fa-solid {
      color: $lightie;
      font-size: 1.5rem;
      padding-right: 1.5rem;
    }
  }
}
